import React  from 'react';
import { Empty, Select,Modal, Button,Pagination, Input} from 'antd';

const { Option } = Select;

function handleChange(value) {
    console.log(`selected ${value}`);
  }
function Wc_2_2(){
    const [visible, setVisible] = React.useState(false);
    const [confirmLoading, setConfirmLoading] = React.useState(false);
    const [modalText, setModalText] = React.useState('Content of the modal');
    
    const showModal = () => {
        setVisible(true);
      };
    
      const handleOk = () => {
        setModalText('The modal will be closed after two seconds');
        setConfirmLoading(true);
        setTimeout(() => {
          setVisible(false);
          setConfirmLoading(false);
        }, 2000);
      };
    
      const handleCancel = () => {
        console.log('Clicked cancel button');
        setVisible(false);
      };
    return(
        <div className="wc_2_1">
            <div className="wc_2_1-header">
                <h2></h2>  
            </div>
            <div className="wc_2_1-bar">
                <div className="wc_2_1-bar-left">
                    <>
                        <Select defaultValue="lucy" style={{ width: 200 }} onChange={handleChange}>
                            <Option value="jack"></Option>
                            <Option value="lucy"></Option>
                        </Select>
                     </>
                </div>
                <div className="wc_2_1-bar-right">
                <>
                        <Button type="primary" onClick={showModal}>
                            
                        </Button>
                        <Modal
                            style={{widli:600}}
                            title=""
                            visible={visible}
                            onOk={handleOk}
                            confirmLoading={confirmLoading}
                            onCancel={handleCancel}
                        >
                           
                                <h2></h2>
                          
                           
                                <Select defaultValue="lucy" style={{ width: 200 }} onChange={handleChange}>
                                    <Option value="jack"></Option>
                                    <Option value="lucy"></Option>
                                    <Option value="Yimingha"></Option>
                                    <Option value="Yiminghb"></Option>
                                    <Option value="Yiminghc"></Option>
                                    <Option value="Yiminghd"></Option>
                                    <Option value="Yiminghe"></Option>
                                    <Option value="Yiminghf"></Option>
                                    <Option value="Yiminghg"></Option>
                                    <Option value="Yiminghh"></Option>
                                    <Option value="Yiminghi"></Option>
                                    <Option value="Yiminghj"></Option>
                                    <Option value="Yiminghk"></Option>
                                    <Option value="Yiminghl"></Option>
                                    <Option value="Yiminghm"></Option>
                                </Select>
                          
                                <Select defaultValue="jack" style={{ width: 150 }} onChange={handleChange}>
                                    <Option value="jack"></Option>
                                    <Option value="lucy"></Option>
                                    <Option value="luco"></Option>
                                    <Option value="lucp"></Option>
                                    <Option value="lucq"></Option>
                                    <Option value="lucrn"></Option>
                                    <Option value="lugs"></Option>
                                    <Option value="lubs"></Option>
                                    <Option value="luby"></Option>
                                    <Option value="lupl"></Option>
                                    <Option value="lulo"></Option>
                                    <Option value="lusfs"></Option>
                                </Select>
                           
                            
                                <Input placeholder="Basic usage" style={{width:240}}/>
                           
                            </Modal>
                        </>
                </div>
            </div>
            <div className="wc_2_1-wrapper">
                <div className="wc_2_1-wrapper-1">
                    <div className="wc_2_1-wrapper-cd">
                    <ul>
                            <li></li>
                            <li></li>
                            <li></li>
                            <li></li>
                            <li></li>
                            <li></li>
                            <li></li>
                            <li></li>
                            <li></li>
                            <li></li>
                            <li></li>
                            <li></li>
                            <li></li>
                            <li></li>
                        </ul>
                    </div>
                </div> 
            </div>
           
            <div className="wc_2_1-footer">
           
           
            <>
              <Pagination size="small" total={50} pagination={false} showSizeChanger showQuickJumper />
            </>
                 
            </div>
        </div>
    )
}
export default Wc_2_2